import React, { useEffect } from "react";
import "../styles/map.css";
const { kakao } = window;

export default function Map() {
  useEffect(() => {
    let mapContainer = document.getElementById("map");

    let mapOption = {
      center: new kakao.maps.LatLng(36.3676, 127.388), //  
      level: 3, //   
    };

    let map = new kakao.maps.Map(mapContainer, mapOption);

    //   
    let markerPosition = new kakao.maps.LatLng(36.3676, 127.388);

    //  
    let marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    //     
    marker.setMap(map);

    //     HTML  document element 
    let content =
      '<div class="customoverlay">' +
      '  <a href="https://map.kakao.com/link/map/14543526" target="_blank">' +
      '    <span class="title"></span>' +
      "  </a>" +
      "</div>";

    //    
    let position = new kakao.maps.LatLng(36.3676, 127.388);

    //   
    let customOverlay = new kakao.maps.CustomOverlay({
      map: map,
      position: position,
      content: content,
      yAnchor: 1,
    });

    customOverlay.setMap(map);
  }, []);

  return (
    <div>
      <div>
        <div id="map"></div>
      </div>
    </div>
  );
}
