import { Link } from "react-router-dom";
import ToGo from "./toGo";
import styles from "../styles/header.module.css";

export default function Headerlist({ overMouse, list, link }) {
  let content = null;
  if (list === "") {
    content = (
      <ul className={styles.dep2}>
        <ToGo link="/intro/summary" nav="" />
        <ToGo link="/intro/history" nav=" " />
        <ToGo link="/intro/invite" nav=" " />
      </ul>
    );
  } else if (list === "") {
    content = (
      <ul className={styles.dep2}>
        <ToGo link="/info/schedule" nav=" " />
        <ToGo link="/info/prepare" nav=" " />
        <ToGo link="/info/location" nav=" " />
        <ToGo link="/info/booth" nav=" " />
      </ul>
    );
  } else if (list === "") {
    content = (
      <ul className={styles.dep2}>
        <ToGo link="/news/notice" nav="" />
        <ToGo link="/news/press" nav="" />
        {/* <ToGo link="/news/jumping" nav="" /> */}
      </ul>
    );
  }

  return (
    <li className={styles.lists}>
      <Link
        to={link}
        className={styles.toApply}
        style={{ color: overMouse ? "#59CE8F" : "white" }}
      >
        <p>{list}</p>
      </Link>
      {content}
    </li>
  );
}
